import * as React from "react"
import './our-journey.css'

import Layout from "../../components/layout"
import Seo from "../../components/seo"
import { Link } from "gatsby"

const our_journey = () => (
    <Layout page="our_journey">
        <Seo title="Our Journey" />
        <div className="main">
          <section className="title-div">
              <strong className="title-our-journey">Our Journey</strong>
          </section>
          <section className="section">
            <div className="widthLimit">
              <div className="columns">
                <div className="column is-two-fifths has-text-right">
                  <p className="leftText innerText">SPRING 2009</p>
                  <p className="innerText">Washington High School</p>
                </div>
                <div className="column">
                  <p className="innerText">CIS was founded in 2009 in George Washington High School; by a few high school students who tried to help out the community through volunteering.</p>
                </div>
              </div>
            </div>
          </section>
          <section className="section blue-background is-justify-content-center">
            <div className="widthLimit">
              <div className="columns">
                <div className="column is-two-fifths has-text-right">
                  <p className="leftText innerText">FALL 2015</p>
                  <p className="innerText">Balboa High School</p>
                </div>
                <div className="column">
                  <p className="innerText">Six years after we established, CIS had a bigger team compared to when we started. We decided to move a step forward and built a CIS team in Balboa High School.</p>
                </div>
              </div>
            </div>
          </section>
          <section className="section">
            <div className="widthLimit">
              <div className="columns">
                <div className="column is-two-fifths has-text-right">
                  <p className="leftText innerText">SUMMER 2019</p>
                  <p className="innerText">Official Non-Profit Organization</p>
                </div>
                <div className="column">
                  <p className="innerText">After serving the community for 10 years, we finally registered to be an official non-profit organization with support from our team members and community partners.</p>
                </div>
              </div>
            </div>
          </section>
          <section className="section blue-background is-justify-content-center">
            <div className="widthLimit">
              <div className="columns">
                <div className="column is-two-fifths has-text-right">
                  <p className="leftText innerText">SPRING 2021</p>
                  <p className="innerText">Lowell High School</p>
                </div>
                <div className="column">
                  <p className="innerText">Six years after Balboa High School CIS was established, CIS became a large non-profit organization. We decided to move one step further and expand to Lowell High School.</p>
                </div>
              </div>
            </div>
          </section>
          <section className="section">
            <div className="widthLimit">
              <div className="columns">
                <div className="column is-two-fifths has-text-right">
                  <p className="leftText innerText">FUTURE</p>
                </div>
                <div className="column">
                  <p className="innerText">We are waiting for you! We hope to expand CIS to more high schools in San Francisco. Please <Link to="/about-us/contact-us" className="text-black"><em>contact us </em></Link> if you are interested.</p>
                </div>
              </div>
            </div>
          </section>
          <section className="section">
            <div className="widthLimit2">
              <div className="columns">
                <div className="column is-one-third has-text-centered">
                  <div className="title-2">
                    <em>
                      <strong className="colorBlue">
                        Club Culture
                      </strong>
                    </em>
                  </div>
                </div>
              </div>
            </div>
          </section>
          <section className="section yellow-background">
            <div className="widthLimit2">
              <div className="columns">
                <div className="column is-one-third has-text-right">
                  <p className="secondSectionInnerText">Members and cultures</p>
                </div>
                <div className="column">
                  <p className="secondSectionInnerDescription">As a multicultural club, we have members from different ethnic groups, with immigrant members as a majority. We have around 100 members between 15 to 20 years old, including high school and college students. </p>
                </div>
              </div>
            </div>
          </section>
          <section className="section">
            <div className="widthLimit2">
              <div className="columns">
                <div className="column is-one-third has-text-centered">
                  <div className="title-2">
                    <em>
                      <strong className="colorBlue">
                        Our Experience
                      </strong>
                    </em>
                  </div>
                </div>
              </div>
            </div>
          </section>
          <section className="section yellow-background">
            <div className="widthLimit2">
              <div className="columns">
                <div className="column is-one-third has-text-right">
                  <p className="secondSectionInnerText">What we have done so far</p>
                </div>
                <div className="column">
                  <p className="secondSectionInnerDescription">We have volunteer experience at a variety of events such as Naturalization Workshop, Chinese New Year Street Fair, Chinatown Community Resources Fair, etc. In addition, We also have self-organized programs such as holiday-themed parties which are designed to serve the elderly living in senior homes. We, as youth, deeply feel that it is part of our duty to help people out in our community and assist each other.</p>
                </div>
              </div>
            </div>
          </section>
          <section className="section yellow-background" />
          <section className="section" />
        </div>
    </Layout>
)

export default our_journey
